body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1440px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1440px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 30px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

@font-face {
  font-family: "Ubuntu R";
  /* Имя шрифта */
  src: url(../fonts/Ubuntu-Regular.ttf) format("truetype"), url(../fonts/Ubuntu-Regular.woff) format("woff"), url(../fonts/Ubuntu-Regular.eot) format("embedded-opentype");
}

@font-face {
  font-family: "Ubuntu M";
  /* Имя шрифта */
  src: url(../fonts/Ubuntu-Medium.ttf) format("truetype"), url(../fonts/Ubuntu-Medium.woff) format("woff"), url(../fonts/Ubuntu-Medium.eot) format("embedded-opentype");
}

@font-face {
  font-family: "Ubuntu B";
  /* Имя шрифта */
  src: url(../fonts/Ubuntu-Bold.ttf) format("truetype"), url(../fonts/Ubuntu-Bold.woff) format("woff"), url(../fonts/Ubuntu-Bold.eot) format("embedded-opentype");
}

body {
  overflow-x: hidden !important;
}

.wrap {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px;
  box-sizing: border-box;
}

.container-main {
  max-width: 1000px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.discount {
  margin-bottom: 28px;
  background-color: #00adef;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-family: "Ubuntu R", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 20px;
  /* Text style for "Летние ски" */
}

.discount span {
  font-family: "Ubuntu b", sans-serif;
}

.menu {
  align-self: center;
  margin-top: -7px;
}

.grid--system {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(2, auto);
  justify-content: space-between;
  align-items: center;
}

.flex {
  display: flex;
}

.phone span:first-child {
  display: block;
  margin-top: -2px;
}

.phone span:first-child a {
  text-decoration: none;
  font-family: "Ubuntu R", sans-serif;
  font-size: 20px;
  line-height: 36px;
  color: #1F1F1F;
}

.logo {
  margin-left: 27px;
}

.connect {
  text-align: right;
  position: relative;
  font-family: "Ubuntu R", sans-serif;
  font-size: 13px;
  line-height: 16px;
  margin-top: -5px;
  /* identical to box height, or 123% */
  color: #1F1F1F;
}

.connect::before {
  border-radius: 50%;
  position: absolute;
  content: "";
  margin-left: -12px;
  margin-top: 7px;
  width: 6px;
  height: 6px;
  background-color: #16e213;
}

.call button {
  margin-left: 64px;
  outline: none;
  border: none;
  color: #ffffff;
  font-family: "Ubuntu M", sans-serif;
  font-size: 13px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  width: 172px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  background-color: #ed2d23;
}

.main--screen {
  margin-top: 20px;
  box-sizing: border-box;
  width: 100%;
  background: url(../img/grad.png);
  height: 612px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main--screen h1 {
  color: #ffffff;
  font-family: "Ubuntu M", sans-serif;
  font-size: 54px;
  line-height: 64px;
}

.main--screen p {
  margin: 18px 0 38px;
  color: #ffffff;
  font-family: "Ubuntu R", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}

.film--watch {
  padding: 0 0 0 12px;
  display: flex;
  align-items: center;
  width: 320px;
  height: 72px;
  border-radius: 4px;
  background-color: rgba(0, 36, 50, 0.6);
}

.film--about {
  margin-left: 16px;
}

.film--about div:first-child {
  color: #ffffff;
  font-family: "Ubuntu b", sans-serif;
  font-size: 17px;
  line-height: 24px;
}

.film--about div:last-child {
  color: #ffffff;
  font-family: "Ubuntu R", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
}

.skew--block:nth-child(1)::before {
  position: absolute;
  content: url(../img/hit.png);
  transform: skew(25deg, 0deg);
  top: -15px;
  left: 10px;
}

.skew--block {
  position: relative;
  width: 288px;
  height: 40px;
  align-items: center;
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  transform: skew(-25deg, 0deg);
}

.skew--block:nth-child(2) {
  margin: 12px 0;
}

.container {
  transform: skew(25deg, 0deg);
  font-family: "Ubuntu R", sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: 20px;
  /* Text style for "4,57" */
  color: #1f1f1f;
}

.container span {
  color: #ed2d23;
}

.move {
  margin-left: 70px;
}

.content--screen {
  display: flex;
}

.content--1 {
  margin-top: 135px;
}

.content--2 {
  margin-top: 105px;
  margin-left: 140px;
}

.navigation {
  padding: 17px 0 16px;
  width: 100%;
  border-bottom: 1px solid #d3dfeb;
}

.navigation--flex {
  display: flex;
  align-items: center;
}

.navigation--flex li {
  margin-left: 24px;
  margin-top: -3px;
}

.navigation--flex li a {
  text-decoration: none;
  color: #3d3d3d;
  font-family: "Ubuntu R", sans-serif;
  font-size: 13px;
  font-weight: 400;
}

.navigation--flex li:first-child {
  margin-left: 0px;
}

.active {
  margin-top: 0px!important;
  opacity: 0.6;
  color: #3d3d3d;
  font-family: "Ubuntu R", sans-serif;
  font-size: 13px;
  font-weight: 400;
  position: relative;
}

.active::after {
  display: inline-block;
  content: url(../img/Group.png);
  vertical-align: middle;
  margin-left: 24px;
}

.content--left h2 {
  margin-bottom: 28px;
  max-width: 370px;
  color: #4b4b4b;
  font-family: "Ubuntu M", sans-serif;
  font-size: 15px;
  line-height: 24px;
}

.content--queue {
  margin-bottom: 8px;
  color: #999999;
  font-family: "Ubuntu R", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
}

.flex--corpus {
  display: flex;
  color: #3d3d3d;
  font-family: "Ubuntu M", sans-serif;
  font-size: 15px;
  line-height: 20px;
}

.flex--corpus div:nth-child(2) {
  margin-left: 48px;
}

.black--river--about {
  margin-top: 48px;
}

.map {
  margin-top: 34px;
  position: relative;
  color: #00adef;
  font-family: "Ubuntu R", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  margin-left: 8px;
  display: block;
}

.map::before {
  position: relative;
  top: 4px;
  left: -8px;
  content: url(../img/Shape.svg);
  float: left;
}

.flex--img {
  display: flex;
}

.wrap--content {
  display: flex;
  justify-content: space-between;
}

.img--block {
  margin-left: 20px;
  margin-top: 40px;
}

.img--block img:first-child {
  margin-bottom: 20px;
}

.marker {
  position: relative;
}

.marker::after {
  position: absolute;
  content: url(../img/marker.png);
  top: -28px;
  right: 20px;
}

.items-architecture {
  display: flex;
  flex-wrap: wrap;
}

.about-architecture h2 {
  color: #3d3d3d;
  font-family: "Ubuntu M", sans-serif;
  font-size: 28px;
  font-weight: 500;
  line-height: 32px;
  margin-bottom: 40px;
}

.about-architecture h3 {
  color: #3d3d3d;
  font-family: "Ubuntu R", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
}

.text-architecture {
  color: #3d3d3d;
  font-family: "Ubuntu R", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  max-width: 575px;
  margin-bottom: 35px;
}

.info-architecture {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info-architecture p {
  color: #999999;
  font-family: "Ubuntu R", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
}

.architecture {
  padding-top: 75px;
}

.plan-queue-b {
  width: 280px;
  min-height: 64px;
  border-radius: 4px;
  border: 1px solid #d3dfeb;
  background-color: #ffffff;
  padding: 24px 24px;
  box-sizing: border-box;
  color: #00adef;
  font-family: "Ubuntu M", sans-serif;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  position: relative;
  margin-right: 20px;
}

.plan-queue-b::after {
  position: absolute;
  content: url(../img/plan-queue.svg);
  right: 0;
  top: 0;
}

.d-model {
  width: 280px;
  min-height: 64px;
  border-radius: 4px;
  border: 1px solid #d3dfeb;
  background-color: #ffffff;
  padding: 24px 24px;
  box-sizing: border-box;
  color: #ed2d23;
  font-family: "Ubuntu M", sans-serif;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  position: relative;
}

.d-model::after {
  position: absolute;
  content: url(../img/3d.svg);
  right: 0;
  top: 0;
}

.model-plan {
  margin-top: 42px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.kitchen {
  position: relative;
  grid-column: span 5;
}

.bathroom {
  position: relative;
  grid-column: span 4;
}

.clear-zone {
  position: relative;
  grid-column: span 3;
}

.place-safe {
  position: relative;
  grid-column: span 3;
}

.bathroom-2 {
  position: relative;
  grid-column: span 4;
}

.terrace {
  position: relative;
  grid-column: span 5;
}

.grid-items-abr {
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 20px;
  display: grid;
  max-width: 1000px;
}

.content-abr h2 {
  color: #ffffff;
  font-family: "Ubuntu M", sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
  position: absolute;
  bottom: 21px;
  left: 24px;
}

.advantages-black-river {
  margin-top: 75px;
}

